@import './reset';
@import './font';
@import './variable';
@import './mixin';
@import './basic';

body {
    font-family: 'Roboto', 'Noto Sans KR', sans-serif;
}

.header {
    @include position(fixed, 0, 0, auto, 0, 999);
    background: transparent;
    height: 100px;
    align-items: center;
    border-bottom: $line;
    transition: 0.5s;

    &.on {
        background: #fff;
        border-bottom: none;
    }

    h1 {
        position: absolute;
        top: 0;
        left: 80px;
        line-height: 100px;
    }

    .service {
        position: absolute;
        top: 0;
        right: 80px;
        line-height: 100px;

        align-items: center;

        button {
            @include button(transparent, none, none);
            font-size: 22px;
            vertical-align: middle;
            margin: 0 0 0 5px;
        }

        i.cart {
            position: relative;
            font-size: 30px;
            margin: 0 0 0 10px;

            span {
                position: absolute;
                top: -5px;
                right: -5px;
                padding: 5px;
                background-color: $mc;
                color: $gc;
                font-size: 12px;
                border-radius: 5px;
            }
        }
    }

    .gnb {
        ul {
            display: flex;
            gap: 30px;
            font-size: 14px;
        }
    }
}

.MainSlide {
    .itm {
        position: relative;
        height: 100vh;

        .slogan {
            @include position(absolute, 50%, auto, auto, 50%, auto);
            transform: translate(-50%, -50%);

            >* {
                display: block;
                font-style: normal;
                margin: 0 0 10px 0;
                color: $gc;
                text-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
            }

            em {
                font-size: 14px;
                text-transform: uppercase;
            }

            span {
                font-size: 30px;
                font-weight: 700;
            }

            strong {
                font-size: 18px;
                font-weight: 300;
                margin: 0 0 40px 0;
            }

            a {
                display: inline-block;
                padding: 6px 15px;
                background: #333;
                font-size: 15px;
                text-transform: uppercase;

                &:hover {
                    background: transparent;
                    border: 1px solid #333;
                    padding: 5px 15px;
                }
            }
        }


        &.itm01 {
            @include background('../../public/images/main01.jpg', center, center)
        }

        &.itm02 {
            @include background('../../public/images/main02.jpg', center, center)
        }

        &.itm03 {
            @include background('../../public/images/main03.jpg', center, center)
        }
    }

    .slick-dots {
        @include position(absolute, auto, auto, 100px, 50%, auto);
        transform: translate(-50%, 0);

        >* {
            display: inline-block;

            button {
                width: 100px;
                height: 5px;
                background: $gc;
                margin: 0 2px;

                border: none;
                outline: none;
                font-size: 0;
                cursor: pointer;
            }
        }

        >*.slick-active {
            button {
                background: $mc;
            }
        }
    }
}


.CateList {
    padding: 300px 0 100px 0;

    .CateTitle {
        margin: 0 0 30px 0;
        padding: 100px 0;
        border-bottom: $line;
        text-align: center;
    }

}

.SlideList {
    position: relative;
    padding: $sp;

    .list {
        margin: 0 -10px;
    }

    .itm {
        padding: 0 10px;
    }

    .arrow {
        @include position(absolute, 50%, auto, auto, 50%);
        border: none;
        background: transparent;
        font-size: 0;
        width: 50px;
        height: 50px;
        border-left: $line;
        border-top: $line;
        cursor: pointer;

        &.prev {
            transform: translate(-50%, -50%) rotate(-45deg);
            margin: 0 0 0 -700px;
        }

        &.next {
            transform: translate(-50%, -50%) rotate(135deg);
            margin: 0 0 0 700px;
        }
    }

    .slick-dots {
        margin: 20px 0 0 0;
        text-align: center;

        &>* {
            display: inline-block;
        }

        &>*.slick-active {
            button {
                background: $mc;
            }

        }

        button {
            border: none;
            background: $gc;
            font-size: 0;
            width: 30px;
            height: 5px;
            margin: 0 2px;
        }
    }
}


.mainTab {

    h2 {
        text-align: center;
        font-size: 22px;
        font-weight: 700;
        padding: 150px 0 20px 0;

        &::after {
            content: "";
            display: block;
            margin: 1rem auto;
            width: 50px;
            height: 1px;
            background-color: #999;
        }
    }

    p {
        text-align: center;
        font-size: 15px;
        font-weight: 300;
        margin: 0 0 30px 0;
    }

    .menu {
        display: flex;
        justify-content: center;
        gap: 4px;

        &>* {
            display: inline-block;
            background: $gc;
            padding: 5px 15px;
        }
    }
}

.footer {
    background: #f9f9f9;

    strong {
        display: block;
        font-size: 30px;
        font-weight: 700;
        margin: 0 0 20px 0;
    }

    p {
        line-height: 1.3;
        font-size: 18px;
        font-weight: 300;
    }

    &>ul {
        display: flex;
        // gap: 30px;
        align-items: center;
        justify-content: center;
        padding: 20px 0;
        font-size: 15px;

        li~li::before {
            content: "";
            display: inline-block;
            margin: 0 15px;
            background: #777;
            width: 1px;
            height: 15px;
            vertical-align: bottom;
        }
    }

    .copy ul {
        color: #999;
        font-size: 14px;
        display: flex;
        gap: 8px;
        align-items: center;
        justify-content: center;
        line-height: 1.375;
        font-weight: 300;


        li {
            position: relative;

            &:not(:last-child)::before {
                content: "";
                position: absolute;
                top: 2px;
                right: -4.5px;
                width: 1px;
                height: 13px;
                background: #ddd;
            }

            a:hover {
                color: #333;
            }
        }
    }
}